.eoex-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: var(--header-height);
  background: var(--surface);
  box-shadow: var(--elevation-1);
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0 var(--gutter);
}
.eoex-header__left {
  font-family: 'Google Sans', Arial, sans-serif;
  font-size: 2rem;
  font-weight: bold;
  color: var(--color-primary);
}
.eoex-logo-green {
  color: var(--color-primary);
}
.eoex-header__center {
  flex: 1;
  display: flex;
  justify-content: center;
}
.eoex-search-bar {
  display: flex;
  align-items: center;
  background: var(--surface-alt);
  border-radius: 24px;
  box-shadow: var(--elevation-1);
  padding: 0 1rem;
  min-width: 320px;
  max-width: 480px;
  width: 100%;
  height: 44px;
}
.eoex-search-icon {
  color: var(--text-secondary);
  margin-right: 0.5rem;
}
.eoex-search-scope {
  border: none;
  background: transparent;
  font-size: 1rem;
  color: var(--text-secondary);
  margin-right: 0.5rem;
}
.eoex-search-input {
  flex: 1;
  border: none;
  background: transparent;
  font-size: 1rem;
  outline: none;
  color: var(--text-primary);
}
.eoex-search-shortcut {
  color: var(--text-secondary);
  margin-left: 0.5rem;
  font-size: 0.9rem;
  background: #e0e3e7;
  border-radius: 4px;
  padding: 2px 6px;
}
.eoex-header__right {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.eoex-header-action, .eoex-header-avatar {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 50%;
  transition: background 0.2s;
}
.eoex-header-action:focus, .eoex-header-avatar:focus {
  outline: var(--focus-ring);
}
.eoex-header-action:hover, .eoex-header-avatar:hover {
  background: rgba(66,133,244,0.08);
}
