.eoex-filterbar {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem var(--gutter) 0 var(--gutter);
  background: var(--surface);
  border-bottom: 1px solid var(--surface-alt);
  overflow-x: auto;
}
.eoex-filterbar__btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}
.eoex-filterbar__btn:focus {
  outline: var(--focus-ring);
}
.eoex-filterbar__btn:hover {
  background: rgba(66,133,244,0.08);
  color: var(--color-accent-blue);
}
.eoex-filterbar__icon {
  font-size: 1.2rem;
}
.eoex-filterbar__tune {
  margin-left: auto;
  background: var(--surface-alt);
  color: var(--color-accent-blue);
  font-weight: bold;
}
@media (max-width: 600px) {
  .eoex-filterbar {
    gap: 0.5rem;
    padding: 0.5rem var(--gutter) 0 var(--gutter);
  }
  .eoex-filterbar__btn {
    font-size: 0.95rem;
    padding: 0.5rem 0.75rem;
  }
}
