.eoex-hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: var(--max-content-width);
  margin: 0 auto;
  padding: var(--section-spacing) var(--gutter);
  background: linear-gradient(120deg, rgba(66,133,244,0.12), rgba(52,168,83,0.12));
  border-radius: var(--radius-card);
  box-shadow: var(--elevation-1);
  min-height: 320px;
  gap: 2rem;
}
.eoex-hero__left {
  flex: 1;
}
.eoex-hero__headline {
  font-family: 'Google Sans', Arial, sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--color-primary);
}
.eoex-hero__subcopy {
  font-size: 1.25rem;
  color: var(--text-secondary);
}
.eoex-hero__right {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.eoex-hero__carousel {
  display: flex;
  gap: 1rem;
}
.eoex-hero__tile {
  background: var(--surface);
  border-radius: var(--radius-card);
  box-shadow: var(--elevation-1);
  overflow: hidden;
  width: 120px;
  height: 160px;
  position: relative;
  transition: box-shadow 0.3s, transform 0.3s;
}
.eoex-hero__tile--active {
  box-shadow: var(--elevation-3);
  transform: scale(1.05);
}
.eoex-hero__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.eoex-hero__badge {
  position: absolute;
  top: 8px;
  left: 8px;
  background: var(--color-accent-blue);
  color: #fff;
  font-size: 0.85rem;
  padding: 2px 8px;
  border-radius: 8px;
  font-weight: 500;
}
@media (max-width: 900px) {
  .eoex-hero {
    flex-direction: column;
    align-items: flex-start;
    min-height: 220px;
  }
  .eoex-hero__right {
    width: 100%;
    justify-content: flex-start;
    margin-top: 1rem;
  }
}
