.eoex-contentgrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
  max-width: var(--max-content-width);
  margin: var(--section-spacing) auto;
  padding: 0 var(--gutter);
}
.eoex-card {
  background: var(--card-surface, #fff);
  border-radius: var(--radius-card);
  box-shadow: var(--elevation-1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.2s, transform 0.2s;
  position: relative;
  outline: none;
}
.eoex-card:focus, .eoex-card:hover {
  box-shadow: var(--elevation-3);
  transform: scale(1.01);
}
.eoex-card__img {
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
  border-radius: var(--radius-card) var(--radius-card) 0 0;
}
.eoex-card__body {
  padding: 1rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.eoex-card__title {
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0;
}
.eoex-card__meta {
  font-size: 0.95rem;
  color: var(--text-secondary);
}
.eoex-card__rating {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 1rem;
  color: var(--color-accent-yellow);
}
.eoex-card__star {
  font-size: 1.1rem;
}
.eoex-card__reviews {
  color: var(--text-secondary);
  font-size: 0.95rem;
}
.eoex-card__tags {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}
.eoex-card__tag {
  background: var(--surface-alt);
  color: var(--text-secondary);
  font-size: 0.85rem;
  padding: 2px 8px;
  border-radius: 8px;
}
.eoex-card__install {
  background: var(--color-primary);
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 0.5rem 1.25rem;
  font-size: 1rem;
  font-weight: 600;
  margin-top: 0.5rem;
  cursor: pointer;
  transition: background 0.2s;
}
.eoex-card__install:hover {
  background: #258a3a;
}
.eoex-card__overflow {
  position: absolute;
  top: 8px;
  right: 8px;
  background: none;
  border: none;
  color: var(--text-secondary);
  border-radius: 50%;
  padding: 4px;
  cursor: pointer;
  transition: background 0.2s;
}
.eoex-card__overflow:focus, .eoex-card__overflow:hover {
  background: rgba(66,133,244,0.08);
  color: var(--color-accent-blue);
}
@media (max-width: 900px) {
  .eoex-contentgrid {
    gap: 1rem;
  }
}
@media (max-width: 600px) {
  .eoex-contentgrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.75rem;
  }
}
